import React from 'react'
import { StaticQuery, graphql } from 'gatsby'
import { Main } from './styles'
import theme from '../utils/theme'
import Global from '../utils/global'
import Header from '../components/layout/header'
import Footer from '../components/layout/footer'
import { ThemeProvider } from 'styled-components'
import { url, lang } from '../utils/i18n'
import { Menu } from '../utils/contents'

export default ({ children, location }) => {
    return (
        <StaticQuery 
            query={ query }
            render={ data => { 
                const language = lang(location)   
                const { edges } = data.allMarkdownRemark
                const menu = Menu(data)
/*
                const blogs = edges.filter(el => el.node.frontmatter.language !== language)
                const { items } = (language !== 'es' ? menu.node.menu.es : menu.node.menu.en)
                const blogsOther = edges.filter(el => el.node.frontmatter.language === language)
                const itemsOther = (language === 'es' ? menu.node.menu.es : menu.node.menu.en)
                const url_lang = url(location, items, itemsOther, blogs, blogsOther) 
                const url_lang = "sdfgsdfg"
                */
                const url_lang = url(location, edges)
                console.log(url_lang)
                return(
                    <ThemeProvider theme={ theme }>
                        <>
                            <Global />
                            <Header 
                                language={language}
                                url_lang={url_lang}
                            />
                            <Main
                                alignItems='center'
                                justifyContent='center'
                                flex='1'
                                flexDirection='column'
                            >
                                { children }
                            </Main>
                            <Footer 
                                language={language}
                            />
                        </>
                    </ThemeProvider>
                )
            }}
        />
    )
}

const query = graphql`
    query {
        allContentsJson {
            edges {
                node {
                    menu {
                        es {
                            items {
                                title
                                url
                                id
                            }
                        }
                        en {
                            items {
                                title
                                url
                                id
                            }
                        }
                    }
                }
            }
        }
        allMarkdownRemark {
            edges {
                node {
                    frontmatter {
                        language
                        path
                        id
                    }
                }
            }
        }  
    }
`